@use "styles/stylesFunctions" as f;
@use "styles/sassVariables" as v;

.row-styles {
  align-items: center;
  padding-top: 44px;
  padding-bottom: 44px;
  .header {
    margin-bottom: 24px;
    @include f.fontStyles(36px, 38px, 400px, #000, "Tajawal");
  }
  p {
    margin: 0;
    margin-right: 30px;
  }

  .section {
    margin-bottom: 35px;
    h5 {
      @include f.fontStyles(25px, 16px, 800px, #000, "Tajawal");
      font-weight: bolder;
      margin-right: 30px;
    }
    p {
      max-width: 65%;
    }
    .flex {
      display: flex;
      gap: 0 15px;

      max-width: 524px;

      h4 {
        @include f.fontStyles(25px, 16px, 800px, #000, "Tajawal");
        font-weight: bolder;
        margin: 0;
      }
      p {
        font-weight: bolder;
      }
    }
  }
}

.Form {
  @include f.flexLayout(center, flex-start, column, 20px);
  h3,
  label,
  span,
  p {
    margin: 0;
  }
  h3 {
    @include f.fontStyles(45px, 34px, 600, #000000, #0000);
  }
  label {
    width: 100%;
    div {
      margin-top: 9px;
      @include f.flexLayout(space-between, center, row, initial);
      a {
        text-decoration: underline;
        color: #ffaa03;
      }
    }
  }

  input[type="number"],
  input[type="date"] {
    padding: 22px 31px;
    width: 100%;
    border-radius: 5px;
    border: solid 0.5px #d2d2d2;
    @include f.fontStyles(39px, 29px, 300, #000000, #0000);
  }

  textarea {
    width: 100%;
    height: 150px;
    padding: 12px 20px;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px;
    background-color: #d2d2d2;
    font-size: 20px;
    resize: none;
    @include f.rtl(dir, ltr, initial);
    @include f.rtl(dir, initial, rtl);
  }

  input[type="submit"] {
    background-color: #001736;
    border-radius: 5px;
    width: 100%;
    padding-top: 22px;
    padding-bottom: 22px;
    @include f.fontStyles(29px, 22px, 600, #ffffff, #0000);
  }
}
